@tailwind base; 
@tailwind components;
@tailwind utilities;


*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}



body {
  font-weight: 400;
  font-family: "Poppins", sans-serif !important;
  font-family: 'Oi', cursive;
  /* font-size: clamp(0.8rem, 1vw, 1.5rem); */
  width: 100vw;
  overflow-x: hidden;
  }

body .fat-face {
  font-family: 'Abril Fatface', cursive;
  font-weight: 900;
  text-shadow: -1px -1px 0 grey;
  text-shadow: 1px 1px 0 black;
 }

 body .maroon {
    color: maroon;
 }

 body .strong {
  font-weight: bolder;
}




