a {
  color: black;
  text-decoration: none !important;
}

.fallback {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  gap: 3.5em;
  z-index: 1000;
}
